







import Vue from 'vue'
import { ROUTES_MAP } from '@/router'
import { mapState } from 'vuex'

export default Vue.extend({
  name: 'habit',
  methods: {
    onAddHabitClick () {
      this.$router.push(ROUTES_MAP.addHabit)
    }
  },
  computed: {
    ...mapState(['habits'])
  }
})
